

.navbar{
    width: 100%;
    height: 10vh;
    background-color: black;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    position: fixed;
}

.navbar-logo{
    font-family: 'Montserrat', sans-serif;
    color: #FF8000;

}

.navbar-logo:hover{
    rotate: 10deg;
    text-decoration: underline;
    cursor: pointer;
    transition: 0.3s;
}

.navbar-links ul{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 4rem;
    color: white;
}

.navbar-links ul li{
    font-family: "lato", sans-serif;
    list-style-type: none;
}

.navbar-links ul li:hover{
    cursor: pointer;
    color: #FF8000;
}

.x-icon{
    color: white;
}

.list-icon{
    color: white;
}

.trigger-drop-down{
    width: 18rem;
    height: 20rem;
    backdrop-filter: blur(10px);
    background: rgba(0, 0, 0, 0.6);
    position: fixed;
    top: 10vh;
    right: 0;
    z-index: 9999;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-family: "lato", sans-serif;
}

.trigger-drop-down ul{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 1rem;
}

.trigger-drop-down ul li{
    list-style-type: none;
    text-align: center;
    font-size: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 2rem;
}

.trigger-drop-down ul h2{
    text-align: center;
    font-size: 1rem;
    color: #FF8000;
}

.trigger-drop-down ul h2:hover{
    text-decoration: underline;
    rotate: 15deg;
    cursor: pointer;
    transition: 0.25s;
}

.trigger-drop-down ul li:hover{
    text-decoration: underline;
    rotate: 15deg;
    cursor: pointer;
    transition: 0.25s;
}

.drop-down-icon{
    display: none;
}

.hidden{
    display: none;
}

@media(max-width: 767px){
    .navbar-links{
        display: none;
    }

    .drop-down-icon{
        display: contents;
    }
}

@media(max-width: 425px){
    .logo-name{
        font-size: 1rem;
    }
}