.about{
    width: 100%;
    height: 70vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
    text-align: center;
}

.about-description{
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 1rem;
}

.about-description h2{
    font-family: 'Montserrat', sans-serif;
    font-size: 3rem;
}

.about-description h4{
    font-family: "lato", sans-serif;
    font-size: 1rem;
}

.about-description h3{
    font-family: "lato", sans-serif;
    color: red;
}

.about-description h5{
    font-family: "noto sans", sans-serif;
    font-size: 1rem;
}

.about-description p{
    font-family: 'Noto Sans', sans-serif;

}

.about-img{
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

}

.about-img .img-container{
    width: 29rem;
    height: 70%;
    background-image: url("../img/buffet-about.png");
    background-position: center;
    background-size: cover;
    border-radius: 20rem;
}


.business-hours{
    text-align: center;
}

.about-info{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 1rem;
}

.about-add-info{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 1rem;
}

.lunch-buffet{
    text-align: center;
}

.dinner-buffet{
    text-align: center;
}

@media(max-width: 1023px){
    .about-img .img-container{
        height: 60%;
        width: 25rem;
    }

    .about-description{
        width: 45%;
        padding: 1rem;
    }

    .about-img{
        width: 55%;
    }
}

@media(max-width: 767px){
    .about{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column-reverse;
        height: 80vh;
    }

    .about-img{
        width: 100%;
        height: 45%;
    }

    .about-img .img-container{
        width: 80%;
        height: 90%;
    }

    .about-description{
        width: 100%;
        height: 55%;
    }

    .about-description h2{
        font-size: 1.5rem;
    }

    .about-description h4{
        font-size: 1rem;
    }

    .about-description{
        font-size: 1rem;

    }

    .about-info{
        display: flex;
        justify-content: center;
        align-items: flex-start;
        flex-direction: row;
    }

    .business-hours{
        font-size: 0.75rem;
    }
    .lunch-buffet{
        font-size: 0.75rem;
    }

    .dinner-buffet{
        font-size: 0.75rem;
    }
    
    .about-add-info{
        font-size: 0.75rem;
    }

    .about-add-info h5{
        font-size: 0.75rem;
    }
}

@media(max-width: 425px){
    .about{
        height: 95vh;
    }

    .about-description h4{
        font-size: 0.85rem;
    }
}