.landing{
    width: 100%;
    height: 100vh;
    background-color: #0E0E0E;
    display: flex;
}

.landing-img-container{
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-end;
}

.landing-img{
    height: 90%;
    width: 70%;
    background-image: url("../img/buffet.png");
    background-position: center;
    background-size: cover;
}

.landing-description{
    flex: 1;
    color: white;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    gap: 2rem;
}

.landing-description h3 span{
    color: #FF8000;
}

.landing-description h3{
    font-family: "lato", sans-serif;
    font-size: 1.5rem;
}

.landing-description h2{
    font-family: "lato", sans-serif;
    font-size: 2.5rem;
}

.about-button{
    color: white;
    background-color: #0E0E0E;
    border: 1px solid #FF8000;
    padding: 1rem;
    width: 12rem;
    font-family: "lato", sans-serif;
    font-size: 1.25rem;
    border-radius: 30px;
}

.about-button:hover{
    background-color: #FF8000;
    cursor: pointer;
    transition: 0.3s;
}

.landing-buttons{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 3rem;
}

@media(max-width: 1023px){
    .landing-buttons{
        flex-direction: column;
    }
}

@media(max-width: 767px){
    .landing{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }

    .landing-img-container{
        width: 100%;
        height: 60%;
        display: flex;
        justify-content: center;
        align-items: flex-end;
    }
    .landing-img-container .landing-img{
        width: 100%;
        height: 80%;
    }

    .landing-description{
        width: 100%;
        height: 40%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        text-align: center;
        gap: 1rem;
        padding: 1rem;
    }

    .landing-description h2{
        font-size: 1.25rem;
    }

    .landing-buttons{
        gap: 1rem;
    }

    .about-button{
        width: 10rem;
        padding: 0.5rem;
    }
}

@media(max-width: 425px){
    .landing-img-container{
        height: 50%;
    }

    .landing-description{
        height: 50%;
    }
}