.footer{
    width: 100%;
    height: 35vh;
    background-color: black;
    color: white;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    text-align: center;
    gap: 5rem;
    padding-top: 5rem;
}

.footer-nav{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 1rem;
}

.footer-direction{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 1rem;
}

.footer-number{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 1rem;
}

.footer-nav h2{
    font-family: 'Montserrat', sans-serif;
}

.footer-nav h3{
    font-family: "lato", sans-serif;
}

.footer-nav h3:hover{
    cursor: pointer;
    color: #FF8000;
}

.footer-direction h2{
    font-family: 'Montserrat', sans-serif;
}

.footer-direction h3{
    font-family: "lato", sans-serif;
    color: white;
}

.footer-direction a{
    text-decoration: none;
}

.footer-direction a:hover{
    text-decoration-line: underline;
    text-decoration-color: #FF8000;
}

.footer-number h2{
    font-family: 'Montserrat', sans-serif;
}

.footer-number h3{
    font-family: "lato", sans-serif;
}

.social-media{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 1rem;
}

.social-media h2{
    font-family: 'Montserrat', sans-serif;

}

.insta{
    color: white;
}

.facebook{
    color: white;
}

.twitter{
    color: white;
}

.insta:hover{
    color: #cd486b;
    rotate: -35deg;
    cursor: pointer;
    transition: 0.3s;
}

.facebook:hover{
    color: #1877F2;
    rotate: -35deg;
    cursor: pointer;
    transition: 0.3s;
}

.twitter:hover{
    color: #1DA1F2;
    rotate: -35deg;
    cursor: pointer;
    transition: 0.3s;
}

@media(max-width: 767px){
    .footer{
        gap: 1.5rem;
    }

    .footer-nav h2{
        font-size: 1rem;
    }

    .footer-nav h3{
        font-size: 0.85rem;
    }

    .footer-direction h2{
        font-size: 1rem;
    }

    .footer-direction h3{
        font-size: 0.85rem;
    }

    .footer-number h2{
        font-size: 1rem;
    }

    .footer-number h3{
        font-size: 0.85rem;
    }

    .social-media h2{
        font-size: 1rem;
    }
}

@media(max-width: 425px){
    .footer{
        gap: 0.5rem;
        height: 40vh;
    }

    .footer-nav h2{
        font-size: 0.85rem;
    }

    .footer-nav h3{
        font-size: 0.75rem;
    }

    .footer-direction h2{
        font-size: 0.85rem;
    }

    .footer-direction h3{
        font-size: 0.75rem;
    }

    .footer-number h2{
        font-size: 0.85rem;
    }

    .footer-number h3{
        font-size: 0.75rem;
    }

    .social-media h2{
        font-size: 0.85rem;
    }

    .insta{
        height: 1rem;
    }

    .facebook{
        height: 1rem;
    }

    .twitter{
        height: 1rem;
    }
}