.gallary{
    width: 100%;
    height: 120vh;
    background-color: #0E0E0E;
    color: white; 
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.gallary-header{
    width: 100%;
    height: 10%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Montserrat', sans-serif;
    font-size: 1.5rem;
}

.gallary-header h2 span{
    color: #FF8000;
}

.picture-showcase{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.first-row{
    width: 100%;
    height: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 3rem;
}

.second-row{
    width: 100%;
    height: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 3rem;
}

@media(max-width: 767px){
    .gallary-header{
        font-size: 1rem;
        text-align: center;
    }

    .picture-showcase{
        flex-direction: row;
        gap: 1rem;
    }

    .first-row{
        flex-direction: column;
        gap: 1rem;
        width: 50%;
        height: 98%;
        align-items: flex-end;
    }

    .second-row{
        flex-direction: column;
        gap: 1rem;
        width: 50%;
        height: 98%;
        align-items: flex-start;
    }
}