.direction{
    width: 100%;
    height: 70vh;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}

.map{
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}


.direction .description{
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 2rem;
}


.direction .description h2{
    font-family: 'Montserrat', sans-serif;
    font-size: 3rem;
}

.direction .description h4{
    font-family: "lato", sans-serif;
    font-size: 1.5rem;
}

.direction .description h3{
    font-family: "lato", sans-serif;
    font-size: 1rem;
}

.direction .description a{
    text-decoration-line: none;
    color: black;
}

.direction .description a:hover{
    color: #FF8000;
    text-decoration-line: underline;
}

@media(max-width: 1023px){
    .direction .description h2{
        font-size: 2rem;
    }

    .direction .description h4{
        font-size: 1rem;
    }

    .direction .description h3{
        font-size: 0.85rem;
    }
}

@media(max-width: 767px){
    .direction{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column-reverse;
    }

    .map{
        width: 100%;
        height: 60%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    
    .map iframe{
        width: 80%;
        height: 90%;
    }

    .direction .description{
        width: 100%;
        height: 40%;
        gap: 1rem;
        text-align: center;
    }
}

@media(max-width: 425px){
    .direction{
        height: 95vh;
    }
}